 .Navbar {
  display: grid;
  grid-template-columns: auto 1fr;
  align-items: center;
  background-color: var(--color-menu);
}

.Navbar h2 {
  padding: 0.25rem 0.75rem;
  font-size: 2.5rem;
  color: var(--color-oscuro);
  background: var(--color-logo);
  position: relative;
  transition: all 0.3s ease-in-out;
} 
